.result-box {
  display: flex;
  margin-bottom: 2rem;
  justify-content: space-between;

  .biz-img {
    flex-basis: 10%;
  }

  .biz-details {
    flex-basis: 90%;
  }
}

.biz-header {
  margin-left: 1rem;
  color: $primary-color;
  display: flex;
  justify-content: space-between;
  align-items: center;
  border-bottom: 2px solid $primary-color;
  margin-bottom: 1rem;

  h3 {
    margin-bottom: 0.5rem;
    font-size: 1.3rem;
  }

  a {
    margin-bottom: 1rem;

    @include media-query(600px) {
      font-size: 0.7rem;
    }
  }

  display: flex;
}

.biz-body {
  margin-left: 1rem;

  .keywords {
    list-style: none;

    li {
      display: inline-block;
      margin-right: 0.2rem;
      font-size: 0.8rem;
      padding: 0.3rem 0.5rem;
      border-radius: 0.3rem;
    }
  }

  .biz-address {
    p {
      font-size: 0.9rem;
    }

    @include media-query(600px) {
      font-size: 1.1rem;
      margin: 1rem 0;
    }
  }
}

.biz-info {
  .wrapper {
    padding: 1rem;
    border-radius: 0.5rem;
  }

  ul {
    list-style: none;
  }

  li {
    margin-bottom: 0.7rem;
    font-size: 0.8rem;

    @include media-query(600px) {
      font-size: 1rem;
    }
  }
}
